<script>
  import { elasticOut } from "svelte/easing";

  // Contains an array of Javascript objects.
  // Each object contains: 
  // [
  //    {
  //         className: string
  //         image: string (url to image)
  //         members: [{
  //           name: string
  //           major: string
  //           pixelLocationX: number (pixel location on class picture)
  //           pixelLocationY: number (same but on y axis)
  //           linkedIn: string (the url)
  //         },
  //     ...]
  //    },
  // ...]
  // basically an ar
  // order from newest to oldest
  export let pledgeClasses;

  let visible = true;

  let classIndex = 0;
  let curClass = pledgeClasses[classIndex];

  let my, mx, sy, sx;
  $: picY = `${my - 120}px`
  $: picX = `${mx - 80}px`

  function handleMousemove(event) {
		mx = event.clientX;
		my = event.clientY;
	}
</script>

<style>
  .cursor-box {
    position: absolute;
    padding: 80px;
    border: 3px gray dotted;
    /* z-index: 2; */
  }

  .class-img-container {
    position: absolute;
    top: 120px;
  }

  .class-img-container img {
    cursor: pointer;
    z-index: 0;
  }
  
  p {
    z-index: 5;
  }
</style>

<svelte:window bind:scrollY={sy} bind:scrollX={sx} />

<p>mx:{mx}</p>
<p>my:{my}</p>
<p>sx:{sx}</p>
<p>sy:{sy}</p>
    <!-- top: {(my - 50) + "px"};
    left: {(mx - 50) + "px"}; -->

<div 
  class="cursor-box"
  style="
    top: {picY};
    left: {picX};
  "
>
</div>

<div class="class-img-container">
  <img
    on:mousemove={handleMousemove}
    class="class-img"
    src="{curClass.image}"
    alt="{curClass.className}"
  >
</div>